import { Component } from 'react'
import { TextField } from '@material-ui/core'
import { faSortAlphaDown, faSortNumericDown } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

export class StayFilterSearch extends Component {

    state = {
        filterBy: {
            searchTxt: '',
            type: 'all',
            price: 'all',
            sortBy: 'all'
        }
    }

    handleChange = ({ target }) => {
        const { name, value } = target
        const { filterBy } = this.state
        this.setState({ filterBy: { ...filterBy, [name]: value } }, () => {
            const { filterBy } = this.state
            // console.log('filterBy',filterBy)
            this.props.onSetFilter(filterBy)
        })
    }


    render() {
        const { searchTxt, type, sortBy } = this.state
        return (
            <div className="_1xq16jy">
                {/* <TextField
                    type="text"
                    size="small"
                    name="searchTxt"
                    color="secondary"
                    autoComplete="off"
                    value={searchTxt}
                    onChange={this.handleChange}
                /> */}
        <input className="_1xq16jy"  autoComplete="off" type="text" name="searchTxt" value={searchTxt} placeholder="Where are you going?" onChange={this.handleChange} />
            </div >
          
        )
    }
}

